<template>
    <div class="swipper">
        <mt-swipe :auto="2000">
            <mt-swipe-item v-for="(item,index) in list" :key="index">
                <img :src="item.imgUrl" alt="">
            </mt-swipe-item>
            
        </mt-swipe>
    </div>
</template>

<script>

export default {
    name:"HomeSwipper",
    props:{
        list:Array
    }
}
</script>
<style scoped>
    .swipper{
        height: 200px;
    }
    .swipper img{
        width: 100%;
        height: 100%;
    }
</style>


