<template>
    <div class="city">
        <!-- 城市选择头 -->
        <city-header></city-header>
        <!-- 城市选择搜索框 -->
        <city-search :cit="cities"></city-search>
        <!-- chengshi  -->
        <city-list :list="hot" :cit="cities" :letter="letter"></city-list>
        <!-- 城市选择---字母表 -->
        <city-alphabet :cit="cities" @letterHandle="receive"></city-alphabet>
    </div>
</template>
<script>
import axios from 'axios'
import CityHeader from './compontents/Header'
import CitySearch from './compontents/Search'
import CityList from './compontents/List'
import CityAlphabet from './compontents/Alphabet'


export default {
    name:'City',
    components:{
        CityHeader,
        CitySearch,
        CityList,
        CityAlphabet
    },
    data(){
        return {
            hot:[],  //热门城市
            cities:{},
            letter:""
        }
    },
    mounted(){
        this.getCitys();
    },
    methods:{
        getCitys(){
            axios.get("/static/api/city.json")
            .then((res)=>{
                console.log(res)
                var data = res.data.data;
                this.hot = data.hotCities;
                this.cities = data.cities;
            })
        },
        receive(letter){
            this.letter = letter
        }
    }
}
</script>
<style  scoped>

</style>


