<template>
    <div>
         <div class="city-search">
        <input v-model="keyword" type="text" class="search-input" placeholder="请输入城市名或拼音">
        </div>
        <div class="search-content" v-show="keyword" >
            <ul>
                <li class="item" v-for="(item,index) in list" :key="index">
                    {{item.name}}
                </li>
            </ul>
        </div>
    </div>
   
</template>
<script>
export default {
    name:'CitySearch',
    data(){
        return {
            keyword:"",
            list:[]
        }
    },
    props:{
        cit:Object
    },
    //侦听器
    watch:{
        keyword(){
            var arr = [];

            if(this.timer){
                clearTimeout(this.timer)
            }
            this.timer = setTimeout(()=>{
                for(let key in this.cit){

                    this.cit[key].forEach(element => {
                        if(element.name.indexOf(this.keyword) > -1 || element.spell.indexOf(this.keyword) > -1){
                            arr.push(element)
                        }
                    });
                }
                this.list = arr;
            },800)
            

        }
    }
}
</script>
<style scoped>
    .city-search{
        height:40px;
        background-color: #00bcd4;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .search-input{
        height: 30px;
        width: 70%;
        border-radius: 10px;
        text-align: center;
        box-sizing: border-box;
        border: 1px solid #eee;
    }
    .search-content{
        position: absolute;
        background-color: #eee ;
        left: 0;
        right: 0;
        top: 80px;
        bottom: 0;
    }
    .item{
        line-height: 35px;
        padding-left: 10px;
        background: #fff;
        color: #666;
    }

</style>


