<template>
    <div class="icon-list">

        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(item,index) in pages" :key="index">
                <div class="icon" v-for="icon in item" :key="icon.id">
                    <div class="icon-img">
                        <img :src="icon.imgUrl" alt="">
                    </div>
                    <div class="icon-desc">{{icon.desc}}</div>
                </div>
            </mt-swipe-item>
            
        </mt-swipe>
    </div>
</template>
<script>
export default {
    name:"HomeIconList",
    props:{
        list:Array
    },
    computed:{
        //定义计算属性计算出一页显示八个图标显示几页
        pages(){
            let pages = [];
            console.log(this.list)
            this.list.forEach((item,index)=>{
                var page = Math.floor(index / 8);
                if(!pages[page]){
                    pages[page] = []
                }
                pages[page].push(item)
            })
            console.log(pages)
            return pages;

        }
    }
}
</script>
<style scoped>
    .icon-list{
        height: 250px;
        text-align: center;
    }
          
    .icon-list >>> .mint-swipe-indicator{
        opacity: 0.6;
        background: #000;
    }
    .icon-list >>> .mint-swipe-indicators.is-active{
        background: #ccc;
    }
    .icon{
        width: 25%;
        box-sizing: border-box;
        float:left;
        padding: 15px;
    }
    .icon-img img{
        width: 100%;
    }
    .icon-desc{
        margin: 6px 0px;
    }

</style>


