<template>
    <div class="city-list" ref="wrapper">

        <div>
            <!-- 当前城市 -->
            <div class="current">
                <h2 class="title">当前城市</h2>
                <div class="button-list">
                    <div class="button-wrapper">
                        <div class="button">北京</div>
                    </div>
                </div>
            </div> 
            <!--  -->
            <div class="hot">
                <h2 class="title">热门城市</h2>
                <div class="button-list">
                    <div class="button-wrapper" v-for="(item,index) in list" :key="index">
                        <div class="button">{{item.name}}</div>
                    </div>
                    
                </div>
            </div> 

            <!-- 城市列表 -->
            <div class="list" v-for="(item,index) in cit" :key="index" :ref="index">
                <h2 class="title" >{{index}}</h2>
                <ul v-for="(it,index) in item" :key="index">
                    <li class="item">{{it.name}}</li>
                </ul>

            </div>
        </div>
        
    </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
    name:'CityList',
    props:{
        list:Array,
        cit:Object,
        letter:String
    },
    mounted(){
        let wrapper = this.$refs.wrapper
        this.scroll = new BScroll(wrapper)
    },
    watch:{
        letter(){
            this.scroll.scrollToElement(this.$refs[this.letter][0])
        }
    }
}
</script>
<style scoped>

    .title{
        height: 30px;
        background-color: #eee;
        line-height: 30px;
    }
    .button-list{
        display: flex;
        flex-wrap: wrap;
    }
    .button-wrapper{
        width: 33%;
    }
    .button{
        border: 1px solid #ccc;
        box-sizing: border-box;
        border-radius: 5px ;
        text-align: center;
        margin: 5px 15px;
        padding: 5px;
    }
    .item{
        line-height: 30px;
        padding-left: 10px;
    }
    .city-list{
        position: absolute;
        left: 0;
        right: 0;
        top: 80px;
        bottom: 0;
        z-index: -999;
    }
    

</style>


