<template>
    <div class="alphabet">
        <ul class="list" >
            <li class="item" v-for="(item,index) in cit" 
            :key="index" 
            :ref="index"
            @click="handleLetter"
            @touchstart.prevent="kaishi"
            @touchmove="huadong"
            @touchend="jieshu"
            >
            {{index}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"CityAlphabet",
    data(){
        return {
            moveStatus:false
        }
    },
    props:{
        cit:{}
    },
    computed:{
        letters(){
            var arr = [];
            for(let key in this.cit){
                arr.push(key)
            }
            return arr
        }
    },
    updated(){
        this.startY = this.$refs['A'][0].offsetTop
    },
    methods:{
        handleLetter(e){
            var letter = e.target.innerText;

            this.$emit("letterHandle",letter)
        },
        kaishi(){
            this.moveStatus = true;
        },
        huadong(e){
           if(this.moveStatus){
               var touchY = e.touches[0].clientY -100;

               var atop = this.$refs['A'][0].offsetTop;
               var index = Math.floor((touchY - atop) / 20);

               if(index >= 0 && index < this.letters.length){
                   var letter = this.letters[index]
                   this.$emit("letterHandle",letter)
               }
           }

        },
        jieshu(){
            this.moveStatus = false;
        }
    }
}
</script>
<style scoped>
    .alphabet{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .list{
        position: absolute;
        right: 10px;
        top:100px;
    }
    .list .item{
        line-height: 20px;
    }
</style>


